import {
	getSystemInfo
} from "@/utils/uniWrap.js"
export default {
	name: "navTab",
	data() {
		return {
			tabWidth: 0, //每个导航栏的宽度
			langTabWidth: 0,
			currentIndex: 0,
			moveLeft: 0,
			toView: 'id0',
			tabArray: [{
				name: '百货'
			}, {
				name: "衣服"
			}, {
				name: '男装'
			}, {
				name: '鬼子'
			}, {
				name: "手链"
			}, {
				name: '手机'
			}, {
				name: '百货'
			}, {
				name: "衣服"
			}, {
				name: '男装'
			}, {
				name: '鬼子'
			}, {
				name: "手链"
			}, {
				name: '手机'
			}],
		}
	},
	methods: {
		//获取屏幕宽度
		calculationWidth() {
			let _this = this;
			getSystemInfo((e) => {
				_this.tabWidth = e.windowWidth / _this.tabArray.length;
				_this.langTabWidth = e.windowWidth / 5
			})
		},
		changeTab(y) {
			this.$emit('changeTab', y);
			this.currentIndex = y;
			this.moveLeft = y * this.tabWidth;
		},
		langChangeTab(y) {
			let nowIndex = y - 2;
			nowIndex = nowIndex <= 0 ? 0 : nowIndex
			this.toView = `id${nowIndex}`
			this.currentIndex = y;
			this.moveLeft = y * this.langTabWidth;
			this.$emit('changeTab', y);
		}

	},
	created() {
		this.calculationWidth();
	}
}
